*{
    margin: 0px;
    padding: 0px;
    
}

input {
    /* margin-top: 5px; */
    outline: none;
    border: 1px solid #464444;
    height: 25px;
    width: 260px;
    
    
  }
  input[type="radio"]{
    width: 10px;
    
  }

form{
    display: flex;
    flex-direction: column;
    background-color: rgb(154, 201, 234);
    align-items: center;
    justify-content: center;
    width: 290px;
    height: 200px;
    
}

button{
    margin-top: 10px;
    outline: none;
    border: 1px solid #464444;
    height: 25px;
    width: 260px;
    background-color: white;
}


.radio-fields{
  width: 260px;
  display: flex;
  align-items: center;
  font-size: 14px;
  justify-content: space-around;

  
  
}
.radio-div{
  display: flex;
  align-items: center;
  
  
}